var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"no-home-section bg-light-blue"},[_vm._ssrNode("<div class=\"container\"><div class=\"wyswyg w-full\"><h2 class=\"text-4xl text-center mb-12\">POLÍTICA DE PROTECCIÓN DE DATOS</h2> <div class=\"text-justify\"><p class=\"mb-4\">\n          Cada uno de los interesados en pertenecer a la Federación Global De Profesiones (F.G.D.P.) proporciona la información que se le solicita de forma voluntaria (esta información tiene la condición de declaración jurada). La información que los MIEMBROS proporciona a la F.G.D.P. (como son: su nombre, apellido, nacionalidad, estado civil, documento de identidad, ocupación, estudios, domicilio, correo electrónico, teléfono, y demás datos que le sean solicitados), así como la información referida a los rasgos físicos y/o de conducta que lo identifican o lo hacen identificable, conforme a ley, es considerada Datos Personales.\n        </p> <p class=\"mb-4\">\n          Los MIEMBROS proporciona a la F.G.D.P. su consentimiento libre, previo, expreso e informado para que sus Datos Personales sean tratados por ésta, es decir, que puedan ser recopilados, registrados, organizados, almacenados, conservados, elaborados, modificados, bloqueados, suprimidos, extraídos, consultados, utilizados, transferidos o procesados de cualquier otra forma prevista por ley. Esta autorización es indefinida y se mantendrá inclusive después de terminadas las operaciones y/o los Contratos que los MIEMBROS tenga o pueda tener con la F.G.D.P. \n        </p> <p class=\"mb-4\">\n          Los Datos Personales de los MIEMBROS serán almacenados (guardados) en el Banco de Datos de Clientes del cual la F.G.D.P. es titular o en cualquier otro que en el futuro se pueda establecer. La F.G.D.P. ha adoptado las medidas necesarias para mantener segura la información.\n        </p> <p class=\"mb-4\">\n          Al dar esta autorización los MIEMBROS permite que la F.G.D.P.: a) Evalúe su comportamiento en las distintas comisiones en las que ha participado o participara en el futuro. b) Pueda decidir si se integra o se le invita a los distintos proyectos que la F.G.D.P. realiza año con año. c) Le ofrezca otros productos y/o servicios de la F.G.D.P. y/o de terceros (Por ejemplo: cursos en otros países, conferencias, maestrías entre otros), lo que se podrá hacer también mediante terceras personas. d) Le envíe ofertas comerciales, publicidad e información en general de los productos y/o servicios de la F.G.D.P. e) Use, brinde y/o transfiera esta información a terceras personas, dentro o fuera del país, vinculadas o no a la F.G.D.P., nacionales o extranjeras, públicas o privadas en ejercicio de la ley; también, al Gobierno, de acuerdo con las disposiciones legales del lugar de origen de estas entidades y a la operativa que realice la F.G.D.P. en cumplimiento de ellas; asimismo, a empresas de mensajería, auditoría, seguridad, sistemas, entre otras que requieran la información en relación con las funciones de la F.G.D.P., la F.G.D.P. se compromete a que las empresas vinculadas a ésta, así como las de mensajería, auditoría, seguridad, sistemas, entre otras que requieran la información para sus funciones, estén obligadas a salvaguardarla confidencialmente; es decir, no revelarán información alguna de los MIEMBROS, salvo en situaciones expresamente previstas en la ley.\n        </p> <p class=\"mb-4\">\n          Los MIEMBROS pueden revocar el consentimiento para tratar sus Datos Personales en cualquier momento. Para ejercer este derecho o cualquier otro que la ley establezca con relación a sus Datos Personales, deberá presentar una solicitud escrita en nuestras oficinas o enviarla por correo electrónico a la siguiente dirección federacionglobal@gmail.com, se podrán establecer otros canales para tramitar estas solicitudes, lo que será informado oportunamente por la F.G.D.P. en su página web.\n        </p></div></div></div>")])}
